.zero {
    height: 5px;
    margin-top: 10px;
    width: 5%;
    background: linear-gradient(124deg, #5f0000, rgb(232, 87, 29));
    background-size: 150% 150%;
    
    -webkit-animation: rainbow 5s ease infinite;
    -z-animation: rainbow 5s ease infinite;
    -o-animation: rainbow 5s ease infinite;
    animation: rainbow 5s ease infinite;
}

.one {
    height: 5px;
    margin-top: 10px;
    width: 25%;

    background: linear-gradient(124deg, rgb(232, 87, 29), #e5e81d);
  background-size: 150% 150%;
  
  -webkit-animation: rainbow 5s ease infinite;
  -z-animation: rainbow 5s ease infinite;
  -o-animation: rainbow 5s ease infinite;
    animation: rainbow 5s ease infinite;
}

.two {
    height: 5px;
    margin-top: 10px;
    width: 50%;
    background: linear-gradient(124deg, #e5e81d, #79b87e);
    background-size: 150% 150%;
    
    -webkit-animation: rainbow 4s ease infinite;
    -z-animation: rainbow 4s ease infinite;
    -o-animation: rainbow 4s ease infinite;
      animation: rainbow 4s ease infinite;
}

.three {
    height: 5px;
    margin-top: 10px;
    width: 75%;
    background: linear-gradient(124deg, #79b87e, #5de81d);
  background-size: 150% 150%;
  
  -webkit-animation: rainbow 4s ease infinite;
  -z-animation: rainbow 4s ease infinite;
  -o-animation: rainbow 4s ease infinite;
    animation: rainbow 4s ease infinite;
}

.four {
    height: 5px;
    margin-top: 10px;
    width: 100%;
    height: 5px;
    margin-top: 10px;
    background: linear-gradient(124deg, #5de81d, #294ff8);
  background-size: 150% 150%;
  
  -webkit-animation: rainbow 4s ease infinite;
  -z-animation: rainbow 4s ease infinite;
  -o-animation: rainbow 4s ease infinite;
    animation: rainbow 4s ease infinite;
}

@-webkit-keyframes rainbow {
    0%{background-position:0% 82%}
    50%{background-position:100% 19%}
    100%{background-position:0% 82%}
}
@-moz-keyframes rainbow {
    0%{background-position:0% 82%}
    50%{background-position:100% 19%}
    100%{background-position:0% 82%}
}
@-o-keyframes rainbow {
    0%{background-position:0% 82%}
    50%{background-position:100% 19%}
    100%{background-position:0% 82%}
}
@keyframes rainbow { 
    0%{background-position:0% 82%}
    50%{background-position:100% 19%}
    100%{background-position:0% 82%}
}